import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Empty from './Empty';

const closeBgStyle =
  'fixed w-full inset-0 bg-black bg-opacity-0 z-30 cursor-default transition-opacity duration-200 ease-in-out';
const openBgStyle =
  'fixed w-full inset-0 bg-black bg-opacity-40 z-30 cursor-default transition-opacity duration-200 ease-in-out';
const closeMainStyle =
  'fixed w-full pc:w-3/5 top-0 -right-full bottom-0 flex flex-col items-end bg-white tb:shadow-mode p-6 pc:pt-28 pc:pb-14 z-40 transition-right duration-200 ease-in-out';
const openMainStyle =
  'fixed w-full pc:w-3/5 top-0 right-0 bottom-0 flex flex-col items-end bg-white tb:shadow-mode p-6 pc:pt-28 pc:pb-14 z-40 transition-right duration-200 ease-in-out';

class Tool extends Component {
  constructor(props) {
    super(props);
    const { mode } = this.props;
    this.state = {
      currentMode: mode,
      popup: false,
      id: 0,
      date: '',
      inputData: '',
      editInput: '',
      bgStyle: closeBgStyle,
      mainStyle: closeMainStyle,
    };
  }

  componentDidMount() {
    this.getNoteData();

    setTimeout(() => {
      this.setState({
        bgStyle: openBgStyle,
        mainStyle: openMainStyle,
      });
    }, 100);

    document.body.style.overflow = 'hidden';
    document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape') {
        this.closeStyle();
      }
    });
  }

  componentWillUnmount() {
    document.body.removeAttribute('style');
    document.removeEventListener('keydown', (e) => {
      if (e.key === 'Escape') {
        this.closeStyle();
      }
    });
  }

  closeStyle = () => {
    const { onClose } = this.props;

    this.setState({
      bgStyle: closeBgStyle,
      mainStyle: closeMainStyle,
    });

    setTimeout(() => {
      onClose();
    }, 300);
  };

  getNoteData = () => {
    const { data, select } = this.props;

    if (data) {
      const copiedData = Array.from(data);
      const selectedId = copiedData.findIndex((i) => i.id === select);

      const thisNoteData = copiedData[selectedId];

      this.setState({
        id: selectedId,
        date: thisNoteData.date,
        inputData: thisNoteData.note,
      });
    }
  };

  modeChecker = () => {
    const { currentMode, inputData } = this.state;
    let content = null;

    if (currentMode === 'ADD') {
      content = (
        <textarea
          name="content"
          className="w-full flex-auto my-6 pc:mt-0 border p-4 pc:px-6 pc:py-9 font-noto text-text text-base overflow-auto resize-none focus:outline-none"
          placeholder="Input your Note in here!"
          onChange={this.getInputData}
        />
      );
    } else if (currentMode === 'VIEW') {
      content = (
        <div className="relative w-full flex-auto my-6 pc:mt-0 border px-4 pc:px-6 font-noto text-text text-base overflow-auto">
          <div className="my-4 pc:my-9 break-all">{this.dataChanger()}</div>
        </div>
      );
    } else if (currentMode === 'EDIT') {
      content = (
        <textarea
          name="content"
          className="w-full flex-auto my-6 pc:mt-0 border p-4 pc:px-6 pc:py-9 font-noto text-text text-base overflow-auto resize-none focus:outline-none"
          placeholder="Input your Note in here!"
          value={inputData}
          onChange={this.getInputData}
        />
      );
    }

    return content;
  };

  btnChecker = () => {
    const { currentMode, inputData, editInput } = this.state;
    let tool = null;

    if (currentMode === 'ADD') {
      tool = (
        <div className="text-2xl sm:text-3xl tb:text-4xl pc:mt-4">
          <button className="text-blue mr-6" type="submit">
            DONE
          </button>
          <button
            className="text-cancel"
            type="button"
            onClick={this.closeStyle}
          >
            CANCEL
          </button>
        </div>
      );
    } else if (currentMode === 'VIEW') {
      tool = (
        <div className="text-2xl sm:text-3xl tb:text-4xl pc:mt-4">
          <button
            className="text-blue mr-6"
            type="button"
            onClick={() => {
              this.setState({
                date: this.getNowDate(),
                currentMode: 'EDIT',
                editInput: inputData,
              });
            }}
          >
            EDIT
          </button>
          <button
            className="text-red mr-6"
            type="button"
            onClick={() => {
              this.deleteNote();
              this.closeStyle();
            }}
          >
            DELETE
          </button>
          <button
            className="text-cancel"
            type="button"
            onClick={() => {
              this.closeStyle();
            }}
          >
            CANCEL
          </button>
        </div>
      );
    } else if (currentMode === 'EDIT') {
      tool = (
        <div className="text-2xl sm:text-3xl tb:text-4xl pc:mt-4">
          <button
            className="text-blue mr-6"
            type="button"
            onClick={() => {
              if (inputData.trim()) {
                this.editNote();
                this.setState({
                  currentMode: 'VIEW',
                });
              } else {
                this.setState({
                  popup: true,
                });
              }
            }}
          >
            DONE
          </button>
          <button
            className="text-cancel"
            type="button"
            onClick={() => {
              this.setState({
                inputData: editInput,
                currentMode: 'VIEW',
              });
            }}
          >
            CANCEL
          </button>
        </div>
      );
    }

    return tool;
  };

  getInputData = (e) => {
    const data = e.target.value;
    this.setState({ inputData: data });
  };

  dataChanger = () => {
    const { inputData } = this.state;
    let noteData = null;
    let i = 0;

    noteData = inputData.split('\n').map((line) => {
      let data = null;

      if (line === '') {
        data = <p key={i}>&nbsp;</p>;
      } else {
        data = <p key={i}>{line}</p>;
      }
      i += 1;

      return data;
    });

    return noteData;
  };

  formatTime = (timeData) => {
    let time = null;

    if (timeData < 10) {
      time = `0${timeData}`;
    } else {
      time = timeData;
    }

    return time;
  };

  getNowDate = () => {
    const newDate = new Date();
    const year = newDate.getFullYear();
    const month = this.formatTime(newDate.getMonth() + 1);
    const day = this.formatTime(newDate.getDate());
    const hour = this.formatTime(newDate.getHours());
    const min = this.formatTime(newDate.getMinutes());

    return `${year}-${month}-${day} ${hour}:${min}`;
  };

  editNote = () => {
    const { onEdit } = this.props;
    const { inputData } = this.state;

    onEdit(this.getNowDate(), inputData);
  };

  deleteNote = () => {
    const { onDelete } = this.props;
    const { id } = this.state;

    onDelete(id);
  };

  emptyPopup = () => {
    const { popup } = this.state;

    let content = null;

    if (popup) {
      content = (
        <Empty
          onPopupClose={() => {
            this.setState({ popup: false });
          }}
        />
      );
    }

    return content;
  };

  render() {
    const { onSubmit } = this.props;
    const { currentMode, date, inputData, bgStyle, mainStyle } = this.state;
    return (
      <div className="font-poppins">
        {this.emptyPopup()}
        <button
          type="button"
          className={bgStyle}
          onClick={this.closeStyle}
          onKeyDown={(e) => {
            if (e.key === 'Escape') {
              this.closeStyle();
            }
          }}
        >
          {' '}
        </button>

        <div className={mainStyle}>
          <div className="text-right text-4xl sm:text-6xl tb:text-7xl text-lightGray">
            {currentMode} NOTE
          </div>
          {currentMode === 'VIEW' && (
            <div className="absolute font-noto text-blue text-sm top-16 sm:top-20 tb:top-24 pc:top-40 pc:-mt-1 pc:left-7 right-8">
              {date}
            </div>
          )}
          <form
            className="flex flex-col flex-auto items-end w-full relative pc:-mt-2 overflow-hidden"
            action="/noteasy"
            method="post"
            onSubmit={(e) => {
              e.preventDefault();
              if (inputData.trim()) {
                onSubmit(this.getNowDate(), inputData);
                this.closeStyle();
              } else {
                this.setState({
                  popup: true,
                });
              }
            }}
          >
            {this.modeChecker()}
            {this.btnChecker()}
          </form>
        </div>
      </div>
    );
  }
}

Tool.propTypes = {
  mode: PropTypes.string.isRequired,
  select: PropTypes.number.isRequired,
  data: PropTypes.arrayOf(PropTypes.object).isRequired,
  onSubmit: PropTypes.func.isRequired,
  onEdit: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default Tool;
