import React, { Component } from 'react';
import './App.css';
import Logo from './Logo';
import Nothing from './Components/Nothing';
import Note from './Components/Note';
import Tool from './Components/Tool';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mode: 'LIST',
      createdId: 0,
      selectedId: 0,
      notes: [],
    };
  }

  componentDidMount() {
    this.getNotes();
  }

  getNotes = () => {
    const notes = JSON.parse(localStorage.getItem('notes'));
    const createdId = JSON.parse(localStorage.getItem('createdId'));

    if (notes) {
      this.setState({ createdId, notes });
    }
  };

  addNote = (_date, _note) => {
    const { createdId, notes } = this.state;
    const copiedNotes = Array.from(notes);
    let id = createdId;
    id += 1;

    copiedNotes.push({
      id,
      date: _date,
      note: _note,
    });

    localStorage.setItem('createdId', id);
    localStorage.setItem('notes', JSON.stringify(copiedNotes));

    this.setState({
      createdId: id,
      notes: copiedNotes,
    });
  };

  editNote = (_date, _note) => {
    const { selectedId, notes } = this.state;
    const copiedNotes = Array.from(notes);
    const id = copiedNotes.findIndex((note) => note.id === selectedId);

    copiedNotes[id].date = _date;
    copiedNotes[id].note = _note;

    localStorage.setItem('notes', JSON.stringify(copiedNotes));

    this.setState({
      notes: copiedNotes,
    });
  };

  deleteNote = (_id) => {
    const { notes } = this.state;
    const copiedNotes = Array.from(notes);

    copiedNotes.splice(_id, 1);

    localStorage.setItem('notes', JSON.stringify(copiedNotes));

    this.setState({
      notes: copiedNotes,
    });
  };

  modeChecker = () => {
    const { mode, selectedId, notes } = this.state;
    let content = null;

    if (mode === 'ADD') {
      content = (
        <Tool
          mode={mode}
          onSubmit={(_date, _note) => {
            this.addNote(_date, _note);
          }}
          onClose={() => {
            this.setState({ mode: 'LIST' });
          }}
        />
      );
    } else if (mode === 'VIEW') {
      content = (
        <Tool
          mode={mode}
          select={selectedId}
          data={notes}
          onSubmit={(_date, _note) => {
            this.addNote(_date, _note);
          }}
          onEdit={(date, note) => {
            this.editNote(date, note);
          }}
          onDelete={(id) => {
            this.deleteNote(id);
          }}
          onClose={() => {
            this.setState({ mode: 'LIST' });
          }}
        />
      );
    }

    return content;
  };

  render() {
    const { notes } = this.state;
    return (
      <div className="flex flex-col pc:flex-row h-screen">
        {this.modeChecker()}
        <div className="sticky pc:static top-0 bg-white w-full pc:w-2/5 z-10">
          <div className="mt-10 mb-4 mx-10 tb:mt-12 tb:mx-16 pc:m-0 pc:fixed pc:top-48 pc:left-20 pcb:left-48">
            <a href="/" className="inline-block">
              <Logo />
            </a>
            <button
              type="button"
              className="block font-poppins text-blue text-lg tb:text-2xl tracking-wider mt-3 pc:mt-12 cursor-pointer"
              onClick={() => {
                this.setState({
                  mode: 'ADD',
                });
              }}
            >
              <span className="mr-1">+</span>
              <span>ADD NOTE</span>
            </button>
          </div>
        </div>

        <main className="flex-col w-full pc:w-3/5 mt-4 pc:mt-48">
          {notes.length === 0 ? (
            <Nothing />
          ) : (
            <div className="w-full px-6">
              {notes.map((note) => (
                <Note
                  key={note.id}
                  date={note.date}
                  note={note.note}
                  onClick={() => {
                    this.setState({
                      mode: 'VIEW',
                      selectedId: note.id,
                    });
                  }}
                />
              ))}
            </div>
          )}
        </main>
      </div>
    );
  }
}

export default App;
