import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Note extends Component {
  dataChanger = () => {
    const { note } = this.props;
    const noteData = note.split('\n');
    let data;
    [data] = noteData;
    if (noteData.length > 1) {
      data += '...';
    }
    return data;
  };

  render() {
    const { date, onClick } = this.props;
    return (
      <button
        type="button"
        className="bg-white p-6 w-full border border-solid border-gray-100 shadow-note mb-7 font-noto overflow-hidden tb:p-9"
        onClick={onClick}
      >
        <div className="text-blue text-sm text-left mb-2 tb:mb-3">{date}</div>
        <div className="text-text text-base text-left whitespace-nowrap overflow-ellipsis overflow-hidden">
          {this.dataChanger()}
        </div>
      </button>
    );
  }
}

Note.propTypes = {
  date: PropTypes.string.isRequired,
  note: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
};

export default Note;
