import React, { Component } from 'react';
import PropTypes from 'prop-types';

const closeStyle =
  'fixed font-noto text-white p-3 left-1/2 bottom-20 transform -translate-x-1/2 bg-black bg-opacity-0 rounded-lg z-30 transition-opacity duration-200 ease-in-out';
const openStyle =
  'fixed font-noto text-white p-3 left-1/2 bottom-24 transform -translate-x-1/2 bg-black bg-opacity-25 rounded-lg z-50 transition-opacity duration-200 ease-in-out';

class Empty extends Component {
  constructor(props) {
    super(props);

    this.state = {
      style: closeStyle,
    };
  }

  componentDidMount() {
    const { onPopupClose } = this.props;

    setTimeout(() => {
      this.setState({
        style: openStyle,
      });
    }, 100);

    setTimeout(() => {
      this.setState({
        style: closeStyle,
      });
    }, 1800);
    setTimeout(() => {
      onPopupClose();
    }, 2000);
  }

  render() {
    const { style } = this.state;

    return <div className={style}>Empty Note.</div>;
  }
}

Empty.propTypes = {
  onPopupClose: PropTypes.func.isRequired,
};

export default Empty;
